<template>
  <dvi>
    <el-row class="search-row" :gutter="20">
      <el-col class="align-left" :span="17">
        <el-button @click="openAddModal" type="primary"></el-button>
      </el-col>
    </el-row>
    <el-row class="table-page-root-outoptions">
      <el-col :span="24">
        <el-table
          :data="originDataList"
          style="width: 100%">
          <el-table-column
            prop="origin_id"
            align="left"
            label="">
          </el-table-column>
          <el-table-column
            prop="origin_name"
            align="left"
            hidden="hidden"
            label="">
          </el-table-column>
          <el-table-column
            prop="parent_origin_id"
            align="left"
            label="">
          </el-table-column>
          <el-table-column
            prop="parent_origin_name"
            align="left"
            label=""
            :formatter="formatterSuperName"
          >
          </el-table-column>

          <el-table-column
            label=""
            align="center">
            <template slot-scope="scope">
              <el-button type="text" @click="handleEdit(scope.$index, scope.row)" size="small"></el-button>
              <el-button type="text" @click="handleDelete(scope.$index, scope.row)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <!--  refreshData:pageCount:-->
    <WorkTablePager @refreshData="getTableData"
                    :pageCount="totalPage">
    </WorkTablePager>
    <!--  -->
    <el-dialog :title="dialogTitle" :visible.sync="showModalPage" >
      <el-form class="modal-form" label-width="20%" :model="formData">
        <el-form-item label="" >
          <treeselect v-model="formSubmitData.parent_origin_id"  :options="originOptions" />
        </el-form-item>
        <el-form-item label="" >
          <el-input placeholder="" v-model="formSubmitData.origin_name" class="input-with-select" ></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-select v-model="formSubmitData.origin_status" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeModal"> </el-button>
        <el-button type="primary" @click="handleInsert"> </el-button>
      </div>
    </el-dialog>
  </dvi>
</template>

<script>
  import WorkTablePager from '@/models/public/WorkTablePager'
  import WorkMain from '@/models/public/WorkMain'
  import { required } from 'vuelidate/lib/validators'
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'

  export default {
    name: 'SubmitAUmanager',
    data () {
      return {
        originDataList: [],
        originDataObjs: {},
        tableDataUrl: 'sys/recordOrigin/listRecordOrigin',
        currPageNum: 1,
        eachPageNum: 10,
        totalPage: 1,
        showModalPage: false,
        isEditModal: false,
        dialogTitle: '',
        formSubmitData: {
          origin_name: '',
          parent_origin_id: '',
          parent_origin_name: '',
          origin_type: '0',
          origin_status: ''
        },
        search: '',
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        filterText: '',
        originOptions: [],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '0',
          label: ''
        }, {
          value: '9',
          label: ''
        }],
        originType: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }]
      }
    },
    watch: {// 
      filterText (val) {
        this.$refs.searchConditionRef.filter(val)
      }
    },
    validations: {// 
      formSubmitData: {
        parent_origin_id: {
          required
        },
        origin_name: {
          required
        },
        origin_status: {
          required
        }
      }
    },
    computed: {
      // 

    },
    components: {
      Treeselect,
      WorkTablePager,
      WorkMain
    },
    methods: {
      getTableData: function (pageNum) {
        if (pageNum && pageNum !== '') {
          this.currPageNum = pageNum
        } else {
          pageNum = this.currPageNum
        }
        const $this = this
        this.BaseRequest({
          url: this.tableDataUrl,
          method: 'get',
          params: {currPage: pageNum, pageSize: this.eachPageNum}
        }).then(response => {
          if (response.dataList != null) {
            response.dataList.forEach(originObj => {
              $this.originDataObjs[originObj.origin_id] = originObj
            })
          }
          $this.originDataList = response.dataList
          $this.totalPage = response.totalPage
        })
      },
      formatterSuperName: function (row) {
        return this.originDataObjs[row.parent_origin_id] != null ? this.originDataObjs[row.parent_origin_id].origin_name : ''
      },
      refreshTableList: function (dataList) {
        this.originDataList = dataList
      },
      openAddModal: function () {
        this.clearData()
        this.dialogTitle = ''
        this.formSubmitData.origin_status = '1'
        // this.formSubmitData.origin_type = 0
        this.getOriginList()
        this.showModalPage = true
        this.isEditModal = false
      },
      closeModal: function () {
        this.showModalPage = false
        this.isEditModal = false
      },
      handleNodeClick (data) { // 
        this.formSubmitData.parent_origin_id = data.id
        this.formSubmitData.parent_origin_name = data.label
      },
      filterNode (value, data) { // 
        if (!value) return true
        return data.name.indexOf(value) !== -1
      },
      handleInsert () { // 
        if (this.checkInputNull()) {
          return
        }
        const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        this.BaseRequest({
          url: 'sys/recordOrigin/addRecordOrigin',
          method: 'post',
          data: this.formSubmitData
        }).then(() => {
          this.Message.success('')
          loading.close()
          this.closeModal()
          this.getTableData()
        }).catch(error => {
          //console.log(error)
          loading.close()
          this.Message.error('' + error)
        })
      },
      handleEdit (index, row) { // 
        this.dialogTitle = ''
        this.showModalPage = true
        this.isEditModal = true
        this.formSubmitData.origin_id = row.origin_id
        this.formSubmitData.origin_name = row.origin_name
        this.formSubmitData.origin_status = row.origin_status
        this.formSubmitData.origin_type = row.origin_type
        this.formSubmitData.parent_origin_id = row.parent_origin_id
        this.formSubmitData.parent_origin_name = this.formatterSuperName(row)
      },
      clearData () { // 
        /* //this.formSubmitData= {};
          // this.formSubmitData.origin_status= '';
          // this.formSubmitData.parent_origin_id= '';
          // this.formSubmitData.parent_origin_name= ''; */
        this.formSubmitData = {
          origin_name: null,
          parent_origin_id: null,
          parent_origin_name: null,
          origin_type: '0',
          origin_status: null
        }
      },
      handleDelete (index, row) { // 
        this.$confirm(""+row.origin_name+"", '', {
          confirmButtonText: '',
          cancelButtonText: '',
          dangerouslyUseHTMLString: true,
          type: 'warning'
        }).then(() => {
          this.BaseRequest({
            url: '/sys/recordOrigin/delById',
            method: 'get',
            params: {'originId': row.origin_id}
          }).then(() => {
            this.Message.success('')
            this.getTableData()
          })
        }).catch(() => {
        })
      },
      checkInputNull () {
        const checkResult = this.$v.$invalid
        if (checkResult) {
          this.$notify({
            dangerouslyUseHTMLString: true,
            message: '<span style="font-size:15px;color:red;font-weight: bold"></span><br>'
          })
        }
        return checkResult
      },
      getOriginList () { // model
        this.BaseRequest({
          url: 'sys/recordOrigin/listAllRecordOrigin',
          method: 'get'
        }).then(response => {
          if (response != null && response.length > 0) {
            let originOptionsTmp = response

            function idToValue(origins){
              origins.forEach(option=>{
                option['value'] = option.id
                if(option.children){
                  idToValue(option.children)
                }
              })
            }
            idToValue(originOptionsTmp)
            // this.originOptions = {
            //   children:originOptionsTmp,
            //   id:"0",
            //   label:"",
            //   originType:"0",
            //   parentId:"0",
            //   value:"0"
            // }
            this.originOptions = originOptionsTmp
          }
        })
      }
    },
    mounted: function () { // 
      this.originDataList = []
      this.getTableData(1)
      this.getOriginList()
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "@/styles/table-page.scss";
  .search-row{
    margin:5px 0 0 0;
  }


</style>



